    .header {
        margin-bottom: 20px;
        padding: 20px 0;
        background: #FFFFFF;
        box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.05); 
        box-sizing: border-box;

        .container {

        }
        .header__wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .logo {

        }
        .header__nav {

        }
        .header__list {
            display: flex;
            justify-content: end;
            align-items: center;
            gap: 40px;
        }
        .header__item {
            a {
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                /* identical to box height */
                text-transform: uppercase;
                color: #1F3773;
            }
        }
        .header__burger-wrapper {
            display: none;
        }
        .header__burger {
            position: relative;
            width: 30px;
            height: 23px;

            display: flex;
            justify-content: center;
            align-items: center;

            &::before,
            &::after {
                content: '';
                width: 100%;
                position: absolute;
                top: 0;
                left: 0;
                height: 3px;
                background: #1F3773;
                display: block;
                transition: .3s;
            }
            &::after {
                top: unset;
                bottom: 0;
            }

            &._active {
                .header__burger-item {
                    opacity: 0;
                }
                &::after {
                    transform: rotate(45deg) translateY(50%);
                    bottom: 50%;
                }
                &::before {
                    transform: rotate(-45deg) translateY(-50%);
                    top: 50%;
                }
            }
        }
        .header__burger-item {
            width: 100%;
            height: 3px;
            background: #1F3773;
            transition: .3s;
        }
    }

