//Тут храняться миксины

@mixin test() {
    background-color: red;
}

@mixin section-header() {
    font-weight: 500;
    font-size: 32px;
    line-height: 38px;
    /* identical to box height */
    color: #1F3773;   
    margin-bottom: 20px; 

    @media screen and (max-width: 730px) {
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;        
    }
}
@mixin section-text() {
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    /* or 125% */
    color: #303030;
    margin: 20px 0;

    @media screen and (max-width: 730px) {
        font-weight: 300;
        font-size: 14px;
        line-height: 18px;        
    }
} 
@mixin section-subhead() {
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    display: block;
    
    color: #1F3773;

    @media screen and (max-width: 590px) {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;        
    }
}
@mixin section-desc() {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    /* or 143% */
    color: #303030;  
    
    @media screen and (max-width: 590px) {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;        
    }
}

@mixin section-btn() {
    display: block;
    border: none;
    outline: none;
    padding: 13px 0 14px;
    box-sizing: border-box;
    max-width: 240px;
    width: 100%;
    background: #1F3773;

    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    
    color: #FFFFFF;    
}